<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/admin" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
        <li>可对非登录账户数据进行修改操作</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="formData.username" :autofocus="true" ref="first" ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" >
          <el-input v-model="formData.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPassword" >
          <el-input v-model="formData.checkPassword" type="password"></el-input>
        </el-form-item>
        <el-form-item label="Email" prop="email">
          <el-input v-model="formData.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="formData.mobile"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="enabled">
          <cms-switch v-model="formData.enabled"></cms-switch>
        </el-form-item>
        <el-form-item label="角色" prop="roleid">
          <el-checkbox-group v-model="formData.roleid">
            <el-checkbox v-for="item in roleList" :key="item.id" :label="item.id">
              {{item.roleName}}
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color >保存并新增</cms-button>
        <cms-button @click="save(true)"  v-color >保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { coreRolePage } from './../../../../api/admin/role.js'
import { adminSave, adminValidEmail, adminValidPhone, adminValidUsername } from './../../../../api/admin/admin.js'

var defaultData = {
  'username': '',
  'password': '',
  'checkPassword': '',
  'email': '',
  'mobile': '',
  'enabled': true,
  'roleid': []
}
export default {
  name: 'adminSave',
  mixins: [form],
  data() {
    var validateEmail = (rule, value, callback) => {
      if (value !== '') {
        adminValidEmail({ validContactMail: value }).then(res => {
          if (res.data) {
            callback()
          } else {
            callback(new Error('该邮箱已被占用'))
          }
        })
      }
    }
    var validatePhone = (rule, value, callback) => {
      if (value !== '') {
        adminValidPhone({ validPhone: value }).then(res => {
          if (res.data) {
            callback()
          } else {
            callback(new Error('该号码已被占用'))
          }
        })
      }
    }
    var validateUsername = (rule, value, callback) => {
      if (value !== '') {
        adminValidUsername({ validUsername: value }).then(res => {
          if (res.data) {
            callback()
          } else {
            callback(new Error('该账户名已被占用'))
          }
        })
      }
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        if (this.type === 'save') {
          callback(new Error('请输入密码'))
        } else {
          callback()
        }
      } else {
        if (this.formData.password !== '') {
          this.$refs.form.validateField('checkPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        if (this.type === 'save') {
          callback(new Error('请再次输入密码'))
        } else {
          callback()
        }
      } else if (value !== this.formData.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      roleList: [],
      formData: Object.assign({}, defaultData),
      rules: {// 新增规则
        username: [rules.required('用户名不能为空'), rules.len(1, 24), rules.custom(validateUsername)],
        password: [rules.required('请输入密码'), rules.password(validatePass), rules.len(6, 16)],
        checkPassword: [rules.required('请再次输入密码'), rules.password(validatePass2)],
        email: [rules.email(), rules.required('邮箱必填'), rules.max(60), rules.custom(validateEmail)],
        mobile: [rules.phone(), rules.required('手机号码必填'), rules.custom(validatePhone)],
        enabled: [rules.required(null, 'blur', 'boolean')],
        roleid: [rules.required('至少勾选一个角色', 'change')]
      }
    }
  },
  methods: {
    save(state) { // 添加
      const params = this.$cms.deepCopy(this.formData)
      params.password = this.$cms.encrypt(params.password)
      delete params.checkPassword
      this.saveForm(adminSave, params, state, '/admin', 'list')
    },
    getRoleList() { // 获取角色列表
      this.loading = true
      coreRolePage({ size: 999999, page: 0 }).then(res => {
        if (res.code === 200) {
          this.roleList = res.data.content
          this.loading = false
        }
      })
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  },
  created() {
    this.getRoleList()
  }
}
</script>




